@use './mixins/fluid' as *;
@use 'functions' as *;

@use "vars" as *;
@use "mixins" as *;
@use "fonts" as *;
@use "settings" as *;


// Header start ========================================================================================================================================================


.header {
  padding-block: 25px;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  margin: 0 auto;
  z-index: 100;
  transition: padding 0.3s ease 0s, background-color 0.3s ease 0s;
  &.scrolling {
    background-color: var(--color-bg-light);
    padding-block: 15px;
    box-shadow: 0 0 5px rgba(255,255,255,0.2);
  }

  // .header__inner

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  // .header__logo

  &__logo {
    @include fluid-text(28, 22);
    position: relative;
    z-index: 100;
    display: flex;
    align-items: flex-end;
    column-gap: fluid(14, 7);
    color: var(--color-white);
    font-weight: 900;
    img {
      @include mobile {
        width: 40px;
        height: 40px;
      }
    }
  }

  // .header__action

  &__action {
    display: flex;
    align-items: center;
    column-gap: fluid(60, 30);
  }
}

.modal-overlay {
  position: fixed;
  inset: 0;
  width: 100%;
  height: 100vh;
  z-index: 111;
  background-color: rgba(0,0,0,0.6);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease 0s, visibility 0.3s ease 0s;
  &-visible {
    opacity: 1;
    visibility: visible;
  }
}

.modal-cart {
  width: 360px;
  height: 100vh;
  background-color: var(--color-bg-light);
  padding: 25px;
  display: none;
  color: var(--color-white);
  justify-self: flex-end;
}

.modal-video {
  display: none;
  color: var(--color-white);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: 80%;
  border: 5px solid var(--color-bg-dark);
  iframe {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    display: block;
  }
}

.modal {
  position: relative;
  &-visible {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.modal-cart .modal-close {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 44px;
  height: 44px;
  border: 1px solid var(--color-white);
  color: var(--color-white);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 5px;
  border-radius: 50%;
  font-size: 28px;
}

.modal-video .modal-close {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 44px;
  height: 44px;
  border: 1px solid var(--color-white);
  color: var(--color-white);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 5px;
  border-radius: 50%;
  font-size: 28px;
}

.menu {
  --paddingLinks: 15px;
  @include tablet {
    transform: translateX(100%);
    transition: transform 0.3s ease 0s;
  }
  &.menu--active {
    transform: translateX(0%);
  }

  @include tablet {
    position: fixed;
    inset: 0;
    z-index: 99;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    background: var(--color-bg-dark);
  }
  > ul {
    display: flex;
    column-gap: fluid(30, 15);
    > li {
      position: relative;
      z-index: 1;
      > a, > button {
        color: var(--color-white);
        font-size: 24px;
        font-weight: 500;
        padding-inline: var(--paddingLinks);
        position: relative;
      }
      > a {
        &:before {
          content: '';
          position: absolute;
          left: 50%;
          top: 100%;
          transform: translateX(-50%);
          height: 2px;
          width: calc(0% - var(--paddingLinks) * 2);
          background-color: var(--color-white);
          transform-origin: 50% 50%;
          transition: width 0.3s ease 0s;
        }
        @include hover {
          &:before {
            width: calc(100% - var(--paddingLinks) * 2);
          }
        }
      }
      > button {
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          right: 0;
          width: 10px;
          height: 10px;
          transform: translateY(-15%);
          transform-origin: 50% 0%;
          background-color: var(--color-white);
          clip-path: polygon(50% 50%, 0 0, 100% 0);
          transition: all 0.3s ease 0s;
        }
        &.menu__btn-active {
          &:after {
            transform: rotate(180deg) translateY(-25%);
          }
        }
      }
    }
    @include tablet {
      width: 100%;
      height: 100%;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      row-gap: fluid(20, 10);
    }
  }

  // .menu__btn

  &__btn {
    cursor: pointer;
    &-active {
      &:after {
        outline: 1px solid red;
      }
    }
  }
}

.dropdown {
  position: absolute;
  left: 0;
  top: 100%;
  z-index: 2;
  margin-left: 15px;
  border: 1px solid rgba(255,255,255,0.5);
  border-radius: 5px;
  width: 100%;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease 0s, visibility 0.3s ease 0s;
  &-active {
    opacity: 1;
    visibility: visible;
  }
  @include tablet {
    position: static;
    opacity: 1;
    visibility: visible;
    border: none;
    text-align: center;
    margin-left: 0;
    display: none;
    &.open {
      display: block;
    }
  }

  // .dropdown__item

  &__item {
    transition: all 0.3s ease 0s;
    background-color: var(--color-bg-light);
    @include tablet {
      background-color: transparent;
    }
    &:not(:last-child) {
      border-bottom: 1px  solid rgba(255,255,255,0.5);
      @include tablet {
        border: 0;
      }
    }
    @include hover {
      background-color: var(--color-bg-dark);
    }
  }

  // .dropdown__link

  &__link {
    display: block;
    width: 100%;
    padding: 10px 15px;
    color: #fff;
    font-size: 14px;
  }
}

.search {
  position: relative;
  display: flex;

  // .search__field

  &__field {
    position: absolute;
    top: 50%;
    right: 0;
    transform-origin: 100% 0%;
    transform: translateY(-50%) scaleX(0);
    transition: transform 0.3s ease 0s;
    @include mobile-s {
      right: -75px;
      z-index: 110;
    }
  }

  // .search__input

  &__input {
    border: none;
    padding: 8px 15px;
    border-radius: 6px;
    box-shadow: 0 0 7px rgba(255,255,255,0.7);
    &:focus-visible {
      outline: 2px dashed var(--color-yellow);
      outline-offset: 2px;
    }
  }

  // .search__btn-close

  &__btn-close {
    position: absolute;
    width: 30px;
    height: 30px;
    top: 50%;
    right: 8px;
    transform: translateY(-50%);
    &:after, &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: #000;
    }
    &:after {
      transform: translateY(-50%) rotate(45deg);
    }
    &:before {
      transform: translateY(-50%) rotate(-45deg);
    }
  }

  // .search__submit

  &__submit {
    width: 26px;
    height: 26px;
    background: url('../img/icon-search.png') center/26px 26px no-repeat;
  }

  &.opened {
    .search__field {
      transform: translateY(-50%) scaleX(1);
    }
  }
}

.burger {
  @include burger;
  position: relative;
  z-index: 100;

  display: none;

  @include tablet {
    display: block;
  }
}

// Header end ========================================================================================================================================================

// Section hero start ========================================================================================================================================================

.hero {
  padding-top: fluid(150, 130);
  padding-bottom: fluid(100, 50);
  background-size: cover;
  background-position: center 50%;
  background-repeat: no-repeat;
  @include tablet {
    background-image: none !important;
  }

  // .hero__content

  &__content {
    display: flex;
    justify-content: space-between;
    column-gap: 30px;
    margin-bottom: fluid(65, 40);
    @include tablet {
      flex-direction: column;
      row-gap: 50px;
    }
  }

  // .hero__body

  &__body {
    max-width: 900px;
    display: flex;
    flex-direction: column;
    row-gap: fluid(240, 150);
    padding-top: 100px;
    @include tablet {
      display: contents;
    }
  }

  // .hero__warp

  &__wrap {
    &:nth-child(1) {
      order: -1;
    }
    &:nth-child(2) {
      order: 3;
    }
    @include tablet {

    }
  }

  // .hero__title

  &__title {
    @include fluid-text(118, 48);

    color: var(--color-white);
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-bottom: 10px;
  }

  // .hero__subtitle

  &__subtitle {
    @include fluid-text(23, 18);

    color: var(--color-white);
    font-weight: 500;
    margin-bottom: 20px;
  }

  // .hero__buttons

  &__buttons {
    display: flex;
    column-gap: 35px;
    align-items: center;
    @include mobile-m {
      flex-direction: column;
      row-gap: 40px;
    }
  }

  // .hero__btn

  &__btn {
    padding: fluid(15, 8) fluid(60, 30);
  }

  // .hero__btn-play

  &__btn-play {
    @include square(64px);

    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid var(--color-white);
    border-radius: 50%;
    transition: background-color 0.3s ease 0s;
    @include hover {
      background-color: var(--color-bg-dark);
    }
  }

  // .hero__video

  &__video {
    display: flex;
    align-items: center;
    column-gap: 16px;
    span {
      color: var(--color-white);

      font-size: 20px;
    }
  }

  // .hero__slider

  &__slider {
    max-width: 512px;
    @include tablet {
      margin: 0 auto;
    }
    @include mobile {
      max-width: 100%;
    }
  }

  // .hero__trandy

  &__trandy {
    h2.title {
      margin-bottom: 130px;
      @include tablet {
        margin-bottom: 190px;
      }
      @include mobile-m {
        margin-bottom: 100px;
      }
    }
  }

  // .hero__item

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 40px;
    border-radius: 151px;
    border: 2px solid rgba(255, 255, 255, 0.27);
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(20px);
    padding: fluid(40, 5);
    &:not(:last-child) {
      margin-bottom: 180px;
      @include mobile-m {
        margin-bottom: 120px;
      }
    }
    &:nth-child(even) {
      flex-direction: row-reverse;
      @include tablet {
        flex-direction: column;
      }
    }
    &:last-child {
      padding-left: fluid(120, 40);
      @include tablet {
        padding-left: fluid(40, 5);
      }
    }
    @include laptop {
      border-radius: 60px;
    }
    @include tablet {
      flex-direction: column;
      border-radius: 30px;
    }
  }

  // .hero__item-image

  &__item-image {
    img {
      margin-top: -200px;
      @include mobile {
        max-width: 500px;
      }
      @include mobile-m {
        max-width: 350px;
        margin-top: -100px;
      }
    }
  }

  // .hero__item-body

  &__item-body {
    width: 50%;
    max-width: 700px;
    @include tablet {
      width: 100%;
      max-width: 100%;
    }
    @include tablet {
      padding: fluid(40, 25);
    }
  }

  // .hero__item-title

  &__item-title {
    @include fluid-text(38, 28);
    color: var(--color-white);
    text-shadow: 0px -9px 80px rgba(0, 0, 0, 0.26), 0px -4.504px 40.039px rgba(0, 0, 0, 0.20), 0px -2.713px 24.118px rgba(0, 0, 0, 0.17), 0px -1.739px 15.456px rgba(0, 0, 0, 0.15), 0px -1.127px 10.017px rgba(0, 0, 0, 0.13), 0px -0.709px 6.306px rgba(0, 0, 0, 0.11), 0px -0.408px 3.623px rgba(0, 0, 0, 0.09), 0px -0.179px 1.595px rgba(0, 0, 0, 0.06);
    font-weight: 600;
    margin-bottom: 24px;
  }

  // .hero__item-subtitle

  &__item-subtitle {
    @include fluid-text(20, 18);

    color: var(--color-white);
    font-weight: 600;
    margin-bottom: 24px;
  }

  // .hero__item-price

  &__item-price {
    @include fluid-text(38, 28);
    color: var(--color-white);
    text-shadow: 0px 37px 80px rgba(0, 0, 0, 0.62), 0px 15.458px 33.422px rgba(0, 0, 0, 0.45), 0px 8.264px 17.869px rgba(0, 0, 0, 0.37), 0px 4.633px 10.017px rgba(0, 0, 0, 0.31), 0px 2.461px 5.32px rgba(0, 0, 0, 0.25), 0px 1.024px 2.214px rgba(0, 0, 0, 0.17);
    font-weight: 600;
    margin-bottom: 24px;
  }

  // .hero__item-buttons

  &__item-buttons {
    display: flex;
    column-gap: 32px;
  }

  // .hero__item-btn

  &__item-btn {
    padding: fluid(15, 8) fluid(58, 25);
  }

  // .hero__item-btn-cart

  &__item-btn-cart {
    @include square(64px);

    border: 2px solid var(--color-white);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 12px;
    transition: background-color 0.3s ease 0s;
    @include hover {
      background-color: var(--color-bg-dark);
    }
  }
}

.testimonials-hero {
  max-width: 410px;
  padding: fluid(30, 20);
  border-radius: 35px;
  border: 2px solid rgba(255, 255, 255, 0.27);
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);

  @include tablet {
    margin: 0 auto;
  }

  // .testimonials-hero__wrap

  &__wrap {
    display: flex;
    align-items: center;
    column-gap: fluid(28, 20);
    margin-bottom: fluid(22, 16);
  }

  // .testimonials-hero__avatar

  &__avatar {
    --widthAvatar: 64px;

    @include square(var(--widthAvatar));

    overflow: hidden;
    border-radius: 50%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }

  // .testimonials-hero__name

  &__name {
    @include fluid-text(22, 18);

    color: var(--color-white);
    margin-bottom: 5px;
  }

  // .testimonials-hero__text

  &__text {
    color: var(--color-white);
    font-size: 17px;
  }
}

.rating {
  display: inline-flex;
  flex-direction: row-reverse;
  &[data-total-value="1"] .rating__item:nth-child(n + 5),
  &[data-total-value="2"] .rating__item:nth-child(n + 4),
  &[data-total-value="3"] .rating__item:nth-child(n + 3),
  &[data-total-value="4"] .rating__item:nth-child(n + 2),
  &[data-total-value="5"] .rating__item:nth-child(n + 1) {
    color: rgba(255, 248, 78, 1);
  }

  // .rating__item

  &__item {
    font-size: 20px;
    color: rgba(255, 248, 78, 0.2);
    cursor: pointer;
    transition: all 0.3s ease 0s;
    &:hover, &:hover ~ & {
      color: rgba(255, 248, 78, 1);
    }
  }
}

.slider-hero {
  padding-top: 120px;

  // .slider-hero__wrapper

  &__wrapper {
    border-radius: 77px;
    border: 2px solid rgba(255, 255, 255, 0.27);
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(20px);
    padding: 0 fluid(25, 10) fluid(25, 10) fluid(25, 10);
    @include mobile {
      border-radius: 30px;
    }
  }

  // .slider-hero__image

  &__image {
    margin-bottom: fluid(36, 20);
    img {
      display: block;
      margin: 0 auto;
      margin-top: -120px;
      position: relative;
      z-index: 100;
      @include mobile-s {
        width: 380px;
      }
    }
  }

  // .hero-slider__body

  &__body {
    padding: 0 fluid(40, 20) 70px;
    @include mobile-s {
      text-align: center;
    }
  }

  // .slider-hero__suptitle

  &__suptitle {
    @include fluid-text(23, 20);
    color: rgba(255, 255, 255, 0.75);
    margin-bottom: 20px;
  }

  // .slider-hero__name

  &__name {
    @include fluid-text(38, 32);
    color: rgba(255, 255, 255, 0.75);
    margin-bottom: 20px;
  }

  // .slider-hero__btn

  &__btn {
    display: block;
    padding: fluid(15, 8) fluid(50, 25);
    @include mobile-s {
      margin: 0 auto;
    }
  }

  .swiper-button-prev {
    display: none;
  }

  .swiper-button-next {
    top: 74%;
    right: 5%;
    &:after {
      content: none;
    }
   }

   .swiper-pagination {
      bottom: 40px;
   }

   .swiper-pagination-bullet {
      width: 6px;
      height: 6px;
      background-color: var(--color-white);
      opacity: 1;
   }

   .swiper-pagination-bullet-active {
      width: 21px;
      border-radius: 6px;
   }
}

// Section hero end ========================================================================================================================================================

// Section products start ========================================================================================================================================================

.products {
  padding: fluid(150, 100) 0 fluid(115, 80) 0;


  // .products__title

  &__title {
    margin-bottom: fluid(125, 100);
  }

  // .products__list

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 80px fluid(40, 20);
    @include tablet-over {
      grid-template-columns: repeat(auto-fit, minmax(40%, 1fr));
    }
    @include mobile {
      grid-template-columns: 1fr;
      justify-self: center;
      max-width: 512px;
      row-gap: 100px;
    }
  }
}

.card {
  border-radius: 77px;
  padding: fluid(50, 30);
  border: 2px solid rgba(255, 255, 255, 0.27);
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  @include laptop {
    border-radius: 50px;
  }

  // .card__image

  &__image {
    margin-bottom: 15px;
    img {
      display: block;
      margin: 0 auto;
      margin-top: -120px;
      position: relative;
      z-index: 100;
    }
  }

  // .card__link

  &__link {
    @include fluid-text(38, 32);
    color: rgba(255, 255, 255, 0.75);
    margin-bottom: fluid(35, 25);
  }

  // .card__descr

  &__descr {
    @include fluid-text(24, 20);
    color: rgba(255, 255, 255, 0.75);
    margin-bottom: fluid(40, 25);
  }

  // .card__fotter

  &__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  // .card__price

  &__price {
    @include fluid-text(38, 32);
    color: rgba(255, 255, 255, 0.75);
  }

  // .card__cart

  &__cart {
    @include square(55px);

    border: 2px solid rgba(255,255,255,0.75);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    transition: background-color 0.3s ease 0s;
    @include hover {
      background-color: var(--color-bg-dark);
    }
  }
}

// Section products end ========================================================================================================================================================

// Section review start ========================================================================================================================================================

.review {
  padding-top: fluid(110, 80);
  padding-bottom: fluid(140, 80);

  // .review__title

  &__title {
    margin-bottom: fluid(125, 100);
  }

  // .review__list

  &__list {
    --gap: 33px;

    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: var(--gap);
    @include tablet-over {
      --gap: 25px;

      flex-wrap: wrap;
    }
    @include mobile {
      justify-content: center;
    }
  }

  // .review__item

  &__item {
    flex-basis: calc((100% / 3) - var(--gap));
    border-radius: 77px;
    border: 2px solid rgba(255, 255, 255, 0.27);
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(20px);
    padding: fluid(50, 20);
    @include tablet-over {
      flex-basis: calc((100% / 2) - var(--gap));
    }
    @include mobile {
      flex-basis: 100%;
      max-width: 512px;
      border-radius: 30px;
    }
  }

  // .review__wrap

  &__wrap {
    display: flex;
    align-items: center;
    column-gap: fluid(35, 25);
    margin-bottom: fluid(60, 40);
    @include mobile {
      flex-direction: column;
      row-gap: 15px;
    }
  }

  // .review__avatar

  &__avatar {
    @include square(82px);

    border-radius: 50%;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }

  // .review__name-starts

  &__name-starts {
    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  // .review__name

  &__name {
    @include fluid-text(38, 24);

    color: var(--color-white);
    white-space: nowrap;
    font-weight: 600;
  }

  // .review__text

  &__text {
    @include fluid-text(24, 18);

    color: rgba(255, 255, 255, 0.75);
  }
}

// Section review end ========================================================================================================================================================

// Section best start ========================================================================================================================================================

.best {
  padding-top: fluid(110, 80);
  padding-bottom: fluid(140, 80);

  // .best__title

  &__title {
    margin-bottom: 20px;
  }

  // .best__slide

  &__slide {
    padding-top: 200px;
    @include tablet {
      padding-top: fluid(200, 150);
    }
  }

  // .best__slide-wrap

  &__slide-wrap {
    border-radius: 92px;
    padding: 50px 50px 50px 0;
    border: 2px solid rgba(255, 255, 255, 0.27);
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(20px);
    display: flex;
    align-items: center;
    column-gap: 30px;
    @include tablet {
      flex-direction: column;
      row-gap: 40px;
      padding: fluid(50, 20);
      border-radius: 50px;
    }
  }

  // .best__slide-image

  &__slide-image {
    flex: 0 1 calc(100% - 740px);
    img {
      margin-top: -220px;
      @include laptop {
        margin-top: -110px;
      }
      @include tablet-over {
        margin-top: 0;
      }
      @include tablet {
        width: 80%;
        display: block;
        margin: 0 auto;
        margin-top: -190px;
      }
      @include mobile-m {
        margin-top: -100px;
      }
    }
    @include tablet-over {
      flex: 0 1 calc(100% - (60% - 30px));
    }
    @include tablet {
      flex: 0 1 100%;
    }
  }

  // .best__slide-body

  &__slide-body {
    flex: 0 1 740px;
    @include tablet-over {
      flex: 0 1 60%;
    }
    @include tablet {
      flex: 0 1 100%;
    }
  }

  // .best__slide-title

  &__slide-title {
    @include fluid-text(38, 24);

    color: rgba(255, 255, 255, 0.75);
    font-weight: 600;
    margin-bottom: fluid(40, 30);
  }

  // .best__slide-content

  &__slide-content {
    p {
      @include fluid-text(28, 14);
      color: rgba(255, 255, 255, 0.75);
      font-weight: 600;
      margin-bottom: fluid(40, 30);
    }
  }

  // .best__slide-btn

  &__slide-btn {
    padding: fluid(15, 10) fluid(60, 40);
    @include mobile-m {
      display: block;
      margin: 0 auto;
    }
  }

  .swiper-button-prev:after,
  .swiper-button-next:after {
    content: none;
  }

  .swiper-button-prev {
    top: auto;
    bottom: 15%;
    left: auto;
    right: 250px;
    @media (max-width: 1600px){
        bottom: 9%;
    }
    @include tablet-over {
      bottom: 14%;
    }
    @include tablet {
      bottom: 0px;
      left: calc(50% - 100px);
    }
  }

  .swiper-button-next {
    top: auto;
    bottom: 15%;
    right: 90px;
    @media (max-width: 1600px){
      bottom: 9%;
    }
    @include tablet-over {
      bottom: 14%;
    }
    @include tablet {
      bottom: 0px;
      right: calc(50% - 100px);
    }
  }

  .swiper {
    padding-bottom: 80px;
  }

  .swiper-pagination {
    width: 65px;
    left: auto;
    bottom: 16%;
    right: 149px;
    color: rgba(255, 255, 255, 0.75);
    font-family: Inter;
    font-size: 20px;
    font-weight: 700;
    @media (max-width: 1600px){
      bottom: 10%;
    }
    @include tablet-over {
      bottom: 15%;
    }
    @include tablet {
      bottom: 10px;
      left: 50%;
      transform: translateX(-50%);
    }
    span.swiper-pagination-total {
      font-size: 15px;
    }
  }
}

// Section best end ========================================================================================================================================================

// Footer start ========================================================================================================================================================

.footer {
  background-color: var(--color-bg-light);

  // .footer__body

  &__body {
    padding-block: fluid(90, 40);
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 40px;
    @include tablet-over {
      grid-template-columns: repeat(3, 1fr);
    }
    @include mobile-m {
      display: flex;
      flex-direction: column;
      row-gap: 40px;
    }
  }

  // .footer__content

  &__content {
    grid-column: span 2;
  }

  // .footer__logo

  &__logo {
    display: inline-flex;
    align-items: flex-end;
    column-gap: 13px;
    margin-bottom: fluid(40, 25);
    span {
      @include fluid-text(58, 36);
      color: var(--color-white);
      font-weight: 900;
    }
  }

  // .footer__descr

  &__descr {
    p {
      @include fluid-text(28, 20);

      color: var(--color-white);
      font-weight: 500;
    }
  }

  // .footer__quick-links

  &__quick-links {
    grid-column: span 1;
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      > li {
        > a {
          @include fluid-text(24, 18);
          color: var(--color-white);
          font-weight: 500;
          text-decoration: none;
          @include hover {
            text-decoration: underline;
          }
        }
        &:not(:last-child) {
          margin-bottom: fluid(26, 16);
        }
      }
    }
  }

  // .footer__title

  &__title {
    @include fluid-text(28, 22);
    color: var(--color-white);
    font-weight: 800;
    margin-bottom: fluid(40, 30);
  }

  // .footer__subscribe

  &__subscribe {
    grid-column: span 2;
    @include mobile {
      grid-column: span 3;
    }
  }

  // .footer__form

  &__form {
    border-radius: 8px;
    display: flex;
    border: 2px solid var(--color-white);
    position: relative;
    overflow: hidden;
    input {
      @include fluid-text(24, 20);

      color: rgba(255, 255, 255, 1);
      font-weight: 500;
      height: 100%;
      padding: fluid(20, 16) 165px fluid(20, 16) fluid(30, 16);
      background-color: transparent;
      outline: none;
      border: none;
    }
  }

  // .footer__submit

  &__submit {
    @include fluid-text(22, 18);
    position: absolute;
    top: 2px;
    right: 2px;
    bottom: 2px;
    border-radius: 5px;
    border: none;
    padding-inline: fluid(25, 16);
    background-color: var(--color-white);
    color: var(--color-bg-light);
    text-transform: uppercase;
    transition: background-color 0.3s ease 0s;
    @include hover {
      background-color: rgba(255,255,255,0.75);
    }
  }

  // .footer__copyright

  &__copyright {
    padding-bottom: fluid(88, 50);
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include mobile-m {
      flex-direction: column;
      row-gap: 40px;
    }
  }

  // .footer__social

  &__social {
    > ul {
      margin: 0;
      padding: 0;
      list-style: none;
      display: flex;
      column-gap: fluid(60, 40);
      > li {
        > a {
          @include fluid-text(28, 22);

          color: var(--color-white);
          font-weight: 900;
          text-decoration: none;
          text-transform: uppercase;
          transition: color 0.3s ease 0s;
          @include hover {
            color: rgba(102, 173, 102);
          }
        }
      }
    }
  }

  // .footer__copy

  &__copy {
    p {
      @include fluid-text(24, 20);

      color: var(--color-white);
      font-weight: 500;
    }
  }
}

// Footer end ========================================================================================================================================================
