:root {
  // base
  --font-family: "Inter", sans-serif;
  --content-width: 1620px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors

  --color-white: #fff;
  --color-yellow: #FFF84E;
  --color-bg-dark: #1B2316;
  --color-bg-light: #222C1D;

}
